import React, { Fragment } from 'react';
import { withRouter, useParams } from 'react-router-dom';
import Media from 'react-media';
import NewsListDesk from '../../components/newsList/newsListDesk/newsListDesk';
import NewsListMob from '../../components/newsList/newsListMob/newsListMob';

const newsFilter = (articles, category) =>
  articles.filter(el => el.category === category);

const NewsCategories = props => {
  const { articles = [] } = props;
  const { category } = useParams();

  return (
    <Fragment>
      {articles && articles.length && (
        <Media query={{ minWidth: 1200 }}>
          {matches =>
            matches ? (
              <NewsListDesk news={newsFilter(articles, category)} />
            ) : (
              <NewsListMob news={newsFilter(articles, category)} />
            )
          }
        </Media>
      )}
    </Fragment>
  );
};

export default withRouter(NewsCategories);
