import React, { Component, Fragment } from 'react';
import { Switch, Route } from 'react-router-dom';
import { connect } from 'react-redux';
import style from './buildingNewsList.module.css';
import newsList from '../../bd/newsContent.json';
import AllNews from './allNews';
import CategoryNews from './newsCategiries';
import NewsDate from './newsDate';
import * as sessionSelectors from '../../redux/session/sessionSelectors';

class BuildingNewsList extends Component {
  state = {
    articles: {},
  };

  componentDidMount = () => {
    window.scrollTo(0, 0);
    this.setState({ articles: newsList });
  };

  render() {
    const { articles } = this.state;
    const { button, locale } = this.props;
    const articlesRender = locale === 'uk' ? articles.newsUk : articles.newsRu;

    return (
      <Fragment>
        <Switch>
          <Route
            path={`/${locale}/news/categories/:category`}
            render={() => <CategoryNews articles={articlesRender} />}
          />
          <Route
            path={`/${locale}/news/date/:date`}
            render={() => <NewsDate articles={articlesRender} />}
          />
          <Route
            exact
            path={`/${locale}/news`}
            render={() => <AllNews articles={articlesRender} />}
          />
        </Switch>
        <button className={style.showMore}>{button}</button>
      </Fragment>
    );
  }
}

const mapStateToProps = state => ({
  locale: sessionSelectors.locale(state),
});

export default connect(mapStateToProps)(BuildingNewsList);
