import React from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import style from './newsNavMob.module.css';
import * as sessionSelectors from '../../../redux/session/sessionSelectors';

const useStyles = makeStyles(() => ({
  root: {
    flexGrow: 1,
    width: '100%',
    backgroundColor: 'transparent',
    '& .MuiAppBar-colorDefault': {
      backgroundColor: 'transparent',
    },
    '& .MuiPaper-elevation4': {
      boxShadow: 'none',
    },
    '& .MuiSvgIcon-root': {
      fill: '#ffffff',
    },
    '& .MuiTabs-indicator': {
      backgroundColor: '#60B6D9',
    },
    '& .MuiTabs-scrollButtons': {
      width: 25,
    },
  },
}));

function ScrollableTabsButtonForce(props) {
  const { newsNav = [], locale } = props;
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <AppBar position="static" color="default">
        <Tabs
          value={value}
          onChange={handleChange}
          variant="scrollable"
          scrollButtons="on"
          indicatorColor="primary"
          textColor="primary"
          aria-label="scrollable force tabs example"
        >
          {newsNav.map(el => (
            <Tab
              key={el.id}
              label={
                <Link className={style.link} to={`/${locale}${el.link}`}>
                  {el.name}
                </Link>
              }
            />
          ))}
        </Tabs>
      </AppBar>
    </div>
  );
}

const mapStateToProps = state => ({
  locale: sessionSelectors.locale(state),
});

export default connect(mapStateToProps)(ScrollableTabsButtonForce);
