import React, { Fragment } from 'react';
import { withRouter, useParams } from 'react-router-dom';
import Media from 'react-media';
import NewsListDesk from '../../components/newsList/newsListDesk/newsListDesk';
import NewsListMob from '../../components/newsList/newsListMob/newsListMob';

const newsFilter = (articles, date) => {
  const year = date.slice(0, 4);
  const month = date.slice(-2);
  const filterdedArticles = articles.filter(el =>
    el.date.includes(`${month}/${year}`),
  );
  return filterdedArticles;
};

const NewsDate = props => {
  const { articles = [] } = props;
  const { date } = useParams();

  return (
    <Fragment>
      {articles && articles.length && (
        <Media query={{ minWidth: 1200 }}>
          {matches =>
            matches ? (
              <NewsListDesk news={newsFilter(articles, date)} />
            ) : (
              <NewsListMob news={newsFilter(articles, date)} />
            )
          }
        </Media>
      )}
    </Fragment>
  );
};

export default withRouter(NewsDate);
